import React from 'react';
import axios from 'axios';

class AddForm extends React.Component{

  state = {
    title:null,
    body:null,
    img:null
  }

_ch=e=>{
     this.setState({[e.target.name]: e.target.value})
   }


  onFormSubmit = (e)=>{
    e.preventDefault() // Stop form submit

    let data=this.state
    console.log(data)
 axios.post('https://cors-anywhere.herokuapp.com/https://yaasoof.000webhostapp.com/PDO/test.php',data)
  .then(res=>console.log(res.data))
  }

  onChangeImg = (e) =>{
    let img = e.target.files[0]
    let red = new FileReader();
    red.readAsDataURL(img);
    red.onload=ev=>{
   this.setState({img:ev.target.result})  
}
  }


render(){
return(
	<React.Fragment>
	<div className="siimple-card " >
	    <div className="siimple-card-header">
        Add New Topic
    </div>
    <div className="siimple-card-body">
        	<form className='siimple-form' onSubmit={this.onFormSubmit}>
<div className="siimple-field">
    <div className="siimple-field-label">Title of Your Pos</div>
    <input name="title" type="text" className="siimple-input siimple-input--fluid" placeholder="Title" onChange = {this._ch}  />
   
</div>
<div className="siimple-field">
    <div className="siimple-field-label">Body of Your Post</div>
    <textarea name="body"  className="siimple-textarea siimple-textarea--fluid"col='10' onChange = {this._ch}  ></textarea>
    
</div>
              <input type="file" onChange={this.onChangeImg} />
        <img src={this.state.img} style = {{"width":"100px","hight":"100px"}} />

    <div className="siimple-field" align="center">
        <input type='submit' className="siimple-btn siimple-btn--success" />    
        <input className="siimple-btn siimple-btn--error" />
    </div>

    </form>
    </div>
</div>
	</React.Fragment>
	)}
}

export default AddForm;
